import {
  okregi,
  sondaze,
  sondazeDoSredniej,
  KOLOR_PARTII,
  glosy2019,
  sredniaSondazy,
} from "./dane";
import {
  Wynik,
  Okreg,
  Sondaze,
  ILE_SYMULACJI,
  WynikDemo,
  Histogram,
} from "./typy";
import {
  odmianaSlowaMandat,
  rysujHistogram,
  rysujWynikWyborow,
} from "./wykresy";
import {
  NaDemo,
  PodzialMandatowKraj,
  PodzialMandatowWOkregu,
  PoparcieWOkregu,
  RozkladPrawdopodobienstwaWOkregu,
} from "./ordynacja";

rysujWynikWyborow(sondaze, "sondaze", { laczOpozycje: false });

const wynikiWgSondazy: Sondaze = {};
Object.entries(sondaze).forEach(
  ([sondaz, wynik]) =>
    (wynikiWgSondazy[sondaz] = PodzialMandatowKraj(wynik as Wynik))
);
wynikiWgSondazy['średnia'] = PodzialMandatowKraj(sredniaSondazy);

rysujWynikWyborow(wynikiWgSondazy, "mandatyKraj", {
poziomo: true,
laczOpozycje: false,
calySejm: true,
});

const podzialWOkregach: Sondaze = {};
const podzialWOkregachDemo: Sondaze = {};
okregi.forEach((okreg) => {
const wynikWOkregu = PoparcieWOkregu(sredniaSondazy, okreg);
podzialWOkregach[okreg.miasto] = PodzialMandatowWOkregu(
    wynikWOkregu,
    okreg
);
podzialWOkregachDemo[okreg.miasto] = NaDemo(
    PodzialMandatowWOkregu(wynikWOkregu, okreg)
);
});

setTimeout(function () {
rysujWynikWyborow(podzialWOkregach, "mandatyWOkregach", {
    poziomo: true,
    laczOpozycje: false,
});
rysujWynikWyborow(podzialWOkregachDemo, "mandatyWOkregachDemo", {
    poziomo: true,
    laczOpozycje: true,
});
}, 200);